import { memo } from "react";
import Tile from "./Tile";

const BLOCK_BODY_H = [
  135, 137, 138, 141, 142, 143, 150, 151, 152, 153,
  154, 155, 156, 158, 159, 145, 147, 131, 131, 156,
];

const BLOCK_BODY_E = [
  134, 139, 139, 140, 140, 140, 149, 148, 149, 149,
  148, 149, 149, 157, 157, 144, 146, 140, 131, 140,
];

function BlocksList(props) {
  const { block } = props;

  return (
    <>
      <Tile
        id={
          block.p === 0
            ? BLOCK_BODY_H[block.t || block.e] || 136
            : BLOCK_BODY_H[block.t || block.e || 0]
        }
        y={block.p === 0 ? block.h - 32 : 0}
      />
      {block.h > 32 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 64 : 32}
        />
      )}
      {block.h > 64 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 96 : 64}
        />
      )}
      {block.h > 96 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 128 : 96}
        />
      )}
      {block.h > 128 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 160 : 128}
        />
      )}
      {block.h > 160 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 192 : 160}
        />
      )}
      {block.h > 192 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 224 : 192}
        />
      )}
      {block.h > 224 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 256 : 224}
        />
      )}
      {block.h > 256 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 288 : 256}
        />
      )}
      {block.h > 288 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 320 : 288}
        />
      )}
      {block.h > 320 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 352 : 320}
        />
      )}
      {block.h > 352 && (
        <Tile
          id={BLOCK_BODY_E[block.t || block.e || 0]}
          y={block.p === 0 ? block.h - 384 : 352}
        />
      )}
    </>
  );
}

export default memo(BlocksList);
