import { G } from "react-native-svg";
import BlocksList from "./BlocksList";

export default function Blocks(props) {
  const { blocks, distance, graphics, infinite } = props;
  const lap = Math.floor(distance / 2600) * 2600;

  return (
    <G x={0 - distance + (infinite ? lap : 0)}>
      <BlocksList blocks={blocks} graphics={graphics} />
    </G>
  );
}
